.modal-creator-content {
  background: rgba(6, 6, 73, 0.8);
  border-radius: 11px;
  display: grid;
  grid-template-columns: 1.3fr 2fr;

  width: 100rem;
  height: 55rem;
  position: absolute;
  backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
}

/* ########################################################################## */

.modal-creator-left-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: 0 0 0.4rem silver;
  border-radius: 11px;
  overflow: hidden;
  transition: all 0.4s;
  background: linear-gradient(113.49deg, #c73d54 -30.3%, #16256b 58.12%);
  position: relative;
  margin: 2.4rem 1.2rem 2.4rem 2.4rem;
}

.modal-creator-banner {
  width: 100%;
  height: 39%;
  object-fit: cover;
  margin-bottom: 6rem;
}

.modal-creator-img {
  width: 25%;
  border: 2px solid #e3590f;
  border-radius: 10px;
  box-shadow: 0 12px 24px rgba(0, 0, 0, 0.2);
  position: absolute;
  left: 50%;
  top: 40%;
  transform: translate(-50%, -50%);
}

.modal-creator-about {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  justify-items: center;
  padding: 2.4rem;
  margin-top: 2.4rem;
}

.modal-creator-name {
  font-size: 3rem;
  color: goldenrod;
  font-weight: 600;
  margin-bottom: 0.4rem;
}

.creator-join-date {
  font-size: 1rem;
  color: #b2abab;
  margin-bottom: 1.6rem;
}

.creator-description {
  text-align: center;
  font-size: 1.4rem;
  margin: 0 1.6rem 2.4rem 1.6rem;
}

.creator-follow-stats {
  display: flex;
  margin-bottom: 1.6rem;
}

.creator-modal-btn-box {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1.6rem;
  margin-bottom: 2rem;
}

.btn-message {
  background-color: #ff4500ff;
  padding: 1.2rem 1.6rem;
  width: 12rem;
}

.btn-follow {
  padding: 0.9rem 1.3rem;
  background-color: transparent;
  border: 3px solid #ff4500ff;
  outline: none;
  width: 12rem;
}

.modal-creator-divider {
  opacity: 30%;
  margin-bottom: 2rem;
  width: 90%;
}

/* ########################################################################## */

.modal-creator-right-content {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 0.3fr 0.5fr 1.5fr 1fr;
  margin: 2.4rem 2.4rem 2.4rem 1.2rem;
  gap: 1.2rem;
}

.creator-analytics-label {
  font-size: 3rem;
  color: #ded6d6;
  font-weight: 600;
  grid-column: 1/-1;
}

.stat-container {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 1.2rem;
  border-radius: 11px;
}

.stat-icon {
  width: 3.6rem;
  height: 3.6rem;
  color: rgb(255, 69, 0);
  pointer-events: none;
}

.stat-text {
  display: flex;
  flex-direction: column;
  gap: 0.8rem;
}

.stat-text p:first-child {
  font-size: 2rem;
  font-weight: 500;
  color: rgb(75, 192, 192);
}

.stat-text p:last-child {
  font-size: 1.2rem;
  color: #b2abab;
}

.bar-chart-container {
  grid-column: 1/3;
  display: flex;
  align-items: center;
  border-radius: 11px;
}

.area-chart-container {
  grid-column: 3/-1;
  border-radius: 11px;
}

.doughnut-chart-container {
  grid-row: 4/-1;
  border-radius: 11px;
}

.analytics-border {
  background: rgba(11, 20, 66, 0.6);
  box-shadow: 0 0 0.4rem silver;
}
